<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <v-list-item>
        <v-text-field
          name="name"
          label="Title"
          id="id"
          v-model="tutorial.title"
        ></v-text-field>
      </v-list-item>
      <v-list-item>
        <v-text-field
          name="name"
          label="Description"
          id="id"
          v-model="tutorial.description"
        ></v-text-field>
      </v-list-item>
      <v-list-item>
        <v-btn color="success" @click="saveTutorial">Submit</v-btn>
      </v-list-item>
    </div>
    <div v-else>
      <h4>You Submitted succesfully!</h4>
      <v-btn color="success" @click="newTutorial">ADD</v-btn>
    </div>
  </div>
</template>

<script>
import TutorialDataService from "../services/TutorialDataService";

export default {
  data() {
    return {
      tutorial: {
        id: null,
        title: "",
        description: "",
        published: false,
      },
      submitted: false,
    };
  },
  methods: {
    saveTutorial() {
      let data = {
        title: this.tutorial.title,
        description: this.tutorial.description,
      };

      TutorialDataService.create(data)
        .then((res) => {
          this.tutorial.id = res.data.id;
          console.log(res.data);
          this.submitted = true;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    newTutorial() {
      this.tutorial = {};
      this.submitted = false;
    },
  },
};
</script>


<style scoped>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>